import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './index.less'
import { connect } from 'react-redux'

import { withRouter } from 'react-router-dom'

import AuthorAvatar from '@/components/web/AuthorAvatar'
import { logout } from '@/redux/user/actions'
import { Button, Icon, Dropdown, Menu } from 'antd'

const DropdownMenu = () => (
  <Menu className="menu">
    <Menu.ItemGroup title="" className="menu-group">
      <Menu.Item></Menu.Item>
      <Menu.Item></Menu.Item>
      <Menu.Item>
        <span></span>
      </Menu.Item>
    </Menu.ItemGroup>
    <Menu.ItemGroup title="" className="menu-group">
      <Menu.Item></Menu.Item>
      <Menu.Item></Menu.Item>
    </Menu.ItemGroup>
  </Menu>
)

@connect(
  null,
  { logout }
)
@withRouter
class AdminHeader extends Component {
  static propTypes = {
    collapsed: PropTypes.bool.isRequired,
    onToggle: PropTypes.func
  }

  handleLogout = () => {
    console.log(this.props)
    this.props.history.push('/login')
  }

  renderDropDownMenu = () => {
    return (
      <Menu className="menu">
        <Menu.Item>
          <span onClick={() => this.props.history.push('/')}></span>
        </Menu.Item>
        <Menu.Item>
          <span onClick={this.handleLogout}></span>
        </Menu.Item>
      </Menu>
    )
  }

  render() {
    const { collapsed } = this.props
    return (
      <div className="admin-header-container">
        <Icon
          type={collapsed ? 'menu-unfold' : 'menu-fold'}
          className="trigger"
          onClick={this.props.onToggle}
        />
        <div className="header-right">
          <Dropdown overlay={this.renderDropDownMenu()}>
            <span>
              <AuthorAvatar size="large" />
            </span>
          </Dropdown>
        </div>
      </div>
    )
  }
}

export default AdminHeader
