import React, { Component } from 'react'
import { Button, DatePicker, Form, Input, InputNumber, Select, Icon } from 'antd'
import FormBuilder from '@/components/helper/FormBuilder'
const Option = Select.Option

// gender - 
const genderOptions = [{ value: 0, label: 'Male' }, { value: 1, label: 'Female' }].map(item => (
  <Option key={item.value} value={item.value}>
    {item.label}
  </Option>
))

/**
 * colon 
 * columns  
 * gutter  columns gutter 
 * element
 *    id | key  isRequired
 *    label  label
 *    tooltip label  tooltip
 *    widget 
 *
 *    fieldProps - getFieldDecorator(id, options)  options FormBuilder 
 *      initialValue 
 *      rules 
 *      ...
 *   formItemProps -  <FormItem {...formItemProps}>
 *      label
 *      formItemLayout...
 *        layout  vertical 
 *
 *
 */
const formMeta = {
  colon: true, //  label  label 
  columns: 1, // 
  // formItemLayout: {},
  elements: [
    {
      key: 'userName',
      label: 'User name',
      tooltip: 'user name',
      initialValue: 'Nate',
      widget: Input,
      rules: [{ required: true, message: '' }],
      formItemProps: {} //  formItemProps
    },
    {
      key: 'password',
      label: 'Password',
      widget: (
        <Input
          type="password"
          prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
          placeholder="please input your password"
        />
      ),
      // widget  props
      required: true //  rules [required: true, message: `${label} is required`]
    },
    {
      key: 'date',
      label: 'Birth date',
      widget: <DatePicker style={{ width: '100%' }} />
    },
    {
      key: 'gender',
      label: 'Gender',
      initialValue: 'female',
      widget: Select,
      children: genderOptions
    },
    {
      key: 'phone',
      label: 'Phone',
      widget: Input,
      required: true,
      rules: [
        {
          pattern: /^\d+$/,
          message: 'Phone must only contain numbers.'
        },
        {
          min: 11,
          message: 'Phone number at least lenght of 11'
        }
      ]
    },
    {
      key: 'age',
      label: 'Age',
      initialValue: 10,
      widget: InputNumber
    },
    {
      key: 'email',
      label: 'Email',
      widget: Input,
      rules: [
        {
          type: 'email',
          message: 'Please input valid email address.'
        }
      ]
    }
  ]
}

class FormSubmitAntd extends Component {
  resetForm = () => {
    this.props.form.resetFields()
  }

  handleSubmit = e => {
    e.preventDefault()
    this.props.form.validateFieldsAndScroll((errors, values) => {
      if (errors) return
      // let params =
      console.log('submit form: ', values)
    })
  }

  render() {
    return (
      <Form onSubmit={this.handleSubmit} layout="horizontal" style={{ width: '400px' }}>
        <FormBuilder meta={formMeta} form={this.props.form}>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
          &nbsp; &nbsp;
          <Button onClick={this.resetForm}>Reset</Button>
        </FormBuilder>
      </Form>
    )
  }
}

export default Form.create()(FormSubmitAntd)
