import axios from 'axios'
import { message } from 'antd'
// import NProgress from 'nprogress'

const instance = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? 'http://127.0.0.1:6060' : 'http://120.79.10.11:6060', // apibase_url
  timeout: 20000 // 
})

let timer

//
instance.interceptors.request.use(
  config => {
    const token = localStorage.getItem('token')
    if (token) {
      config.headers.common['Authorization'] = 'Bearer ' + token
    }
    return config
  },
  error => {
    message.error('bed request')
    Promise.reject(error)
  }
)
//
instance.interceptors.response.use(
  response => {
    if (response.data.code !== 200) {
      response.data.message && message.warning(response.data.message)
      console.log(response.data)
      return Promise.reject(response.data)
    }
    return response.data
  },
  err => {
    clearTimeout(timer)
    timer = setTimeout(() => {
      if (err && err.response) {
        switch (err.response.status) {
          case 400:
            message.error('')
            break
          case 401:
            localStorage.clear()
            message.error('')
            break
          case 403:
            message.error('')
            break
          case 404:
            message.error(',')
            break
          case 500:
            message.err('')
            break
          default:
            message.err(` ${err.response.status}`)
            break
        }
      } else {
        message.error('')
      }
    }, 200) // 200 

    return Promise.reject(err)
  }
)

export default instance
